<script>
    export default {
        data() {
            return {
                step : 0,
                onListPopup : false,
                thrid_number_list : [
                  {
                    num : 1,
                    collect: false,
                  },
                  {
                    num : 2,
                    collect: false,
                  },
                  {
                    num : 3,
                    collect: false,
                  },
                  {
                    num : 4,
                    collect: false,
                  },
                  {
                    num : 5,
                    collect: false,
                  },
                  {
                    num : 6,
                    collect: false,
                  },
                ],
                cards : [
                  {
                    num : 1,
                  },
                  {
                    num : 2,
                  },
                  {
                    num : 3,
                  },
                  {
                    num : 4,
                  },
                  {
                    num : 5,
                  },
                  {
                    num : 6,
                  },
                ],
                selected_num : 0,
                success : false,
                fail : false,
            };
        },
        methods: {
            nextStep(){
              this.step+=1;
              if(this.step==1){
                this.shuffleCards()
                setTimeout(()=>{
                  this.shuffleCards()
                },500)
              }
            },
            prevStep(){
              this.step-=1;
              this.thrid_number_list[0].collect = false;
              this.thrid_number_list[1].collect = false;
              this.thrid_number_list[2].collect = false;
              this.thrid_number_list[3].collect = false;
              this.thrid_number_list[4].collect = false;
              this.thrid_number_list[5].collect = false;
              this.success = false;
              this.fail = false;
            },
            onPopup(num){
              this.onListPopup = !this.onListPopup
              this.selected_num = num;
            },
            onSelectedNumber(num){
              if(this.selected_num == num){
                this.success = true;
                this.thrid_number_list[num-1].collect = true
                let thrid_number_arr = this.thrid_number_list;

                let total_count = thrid_number_arr.length;
                let count_up = 0;
                for(let item of thrid_number_arr){
                  if(item.collect){
                    count_up++;
                  }
                }
                if(total_count == count_up){
                  setTimeout(()=>{
                    this.nextStep();
                  },1000)
                }
              }else{
                this.fail = true;
              }
            },
            closePopup(){
              this.success = false;
              this.fail = false;

              let thrid_number_arr = this.thrid_number_list;
              let total_count = thrid_number_arr.length;
              let count_up = 0;
              for(let item of thrid_number_arr){
                if(item.collect){
                  count_up++;
                }
              }
              if(total_count == count_up){
                setTimeout(()=>{
                  this.nextStep();
                },1000)
              }
            },
            closeNextPopup(){
              this.closePopup();
              this.onListPopup = false;
            },
            shuffleCards() {
              const shuffled = [...this.cards];
              for (let i = shuffled.length - 1; i > 0; i--) {
                  const j = Math.floor(Math.random() * (i + 1));
                  [
                      shuffled[i], shuffled[j]
                  ] = [
                      shuffled[j], shuffled[i]
                  ];
              }
              this.cards = shuffled;
          }
        },
    };
</script>
